.mainHead {
    font-size: 72px;
    max-width: 587px;
    text-align: center;
    margin: auto;
    padding-bottom: 40px;
}

button.mx-auto.d-flex.btn.btn-light.px-5.py-3.rounded-pill {
    color: #B845ED;
    font-weight: 600;
}

.mainPara {
    max-width: 768px;
    font-size: 24px;
    text-align: center;
    margin: auto;
    color: rgb(191 193 201);
    padding-bottom: 40px;
}

.main {
    padding: 100px 20px 100px 20px;
}

.main::before {
    position: absolute;
    content: "";
    z-index: -1;
    top: 0%;
    transform: translate(-50px, -50px);
    bottom: 10%;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(83.21deg, #3245ff 0%, #bc52ee 100%);
    background-blend-mode: overlay;
    -webkit-mask-image: radial-gradient(rgba(0, 0, 0, .5), transparent 70%);
    mask-image: radial-gradient(rgba(0, 0, 0, .5), transparent 70%);
}

.Benner-logo {
    width: 130px;
}

.Benner-text {
    color: rgb(191 193 201);
}

@media only screen and (max-width: 768px) {
    .BENNER-MAIN {
        display: flex;
        flex-wrap: wrap;
    }

    .BENNER-MAIN-1 {
        width: 176px;
    }

    .mainHead {
        font-size: 60px;
        padding-bottom: 30px;
    }
}

@media only screen and (max-width: 430px) {
    .mainHead {
        font-size: 40px;
        padding-bottom: 30px;
    }
    .BENNER-MAIN-1 {
        width: 150px;
    }
    .mainPara {
        font-size: 20px;
        padding-bottom: 20px;
    }

    .main {
        padding: 50px 20px 50px 20px;
    }
}