* {
    padding: 0;
    margin: 0;
    text-shadow: none;
}

.Performance-text h1 {
    font-family: Obviously, obviously-regular-fallback, sans-serif;
    font-size: 3.75rem;
    line-height: 1.25;
    color: white;
    margin-bottom: 40px;
}

.Performance {
    padding: 100px 0;
    background-color: black;
    background-size: 20px 20px;
    background-image: linear-gradient(to right, #23262D 1px, transparent 1px), linear-gradient(to bottom, #23262D 1px, transparent 1px);
    background-position: top center;
    image-rendering: pixelated;
    /* -webkit-mask-image: linear-gradient(to bottom, transparent, 10%, white, 90%, transparent); */
}
.Performance-text p{
    color: #bdbfc7;
    font-family: InterVariable,sans-serif;
    font-size: 1.5rem;
    line-height: 2rem;
    font-weight: 200 !important;
    line-height: 1.5;
    --tw-text-opacity: 1;
    max-width: 768px;
    margin: auto;
}
.prograss-bars{
    max-width: 64rem;
    margin-top: 3rem;
    margin-left: auto;
    margin-right: auto;
    padding: 1.5rem;
    background-color: #25282f;
    border: 1px solid #bdbfc729;
}
.prograss-row{
    display: grid;
    grid-template-columns: 20% auto;
    margin-bottom: 5px;
}
.progress{
    height: 42px;
    border: 1px solid #ffffff;
    padding: 3px;
    background: #000;
}
.progress-bar {
    width: 95%;
    border-radius: 3px;
    background-image: linear-gradient(247.23deg,#4AF2C8 0%,#2F4CB3 100%);
}
.text-end{
    text-align: end;
    margin-right: -40px;
    padding-top: 5px;
}
.prograss-bars p{
    font-family: "MD IO 0.5",md-io-fallback,monospace;
    font-weight: 300;
   font-size: 18px;
   color: #bdbfc7;
   margin-bottom: 30px;
}
.gray {
    background-color: rgb(84 88 100 ) !important;
    --tw-bg-opacity: 1;
    background-image: none;
    width: 68%;
}
.gray-1{
    background-color: rgb(84 88 100 );
    background-image: none;
    width: 63%;
}
.gray-2{
    background-color: rgb(84 88 100 );
    background-image: none;
    width: 58%;
}
.gray-3{
    background-color: rgb(84 88 100 );
    background-image: none;
    width: 54%;
}
.Astro {
    text-align: end;
    margin-right: 20px;
}
.color-pro{
    color: #bdbfc7;
    font-size: 1.5rem;
}
.prograss-bars-h2{
    font-family: "MD IO 0.5",md-io-fallback,monospace;
    font-weight: 300;
   font-size: 18px;
   color: #bdbfc7;
   margin-top: 30px;
}

/* Zero */
.Zero{
    padding: 100px 0;
}
.whoa{
    font-weight: 700;
    margin-left: 10px;
    display: block;
}
.max-w-3xl{
    font-weight: 500;
    max-width: 48rem;
   font-size: 60px;
   margin-bottom: 40px;
}
.container{
    max-width: 1280px;
    width: 100%;
    margin: auto;
}
.tools p{
   color:#bdbfc7 ;
   font-family: "MD IO 0.5",md-io-fallback,monospace;
   font-weight: 300;
   font-size: 18px;
}
.tools img{
    width: 100%;
    max-width: 100px;
}
.marq-b-twxt h2{
    font-size: 40px;
    color: #f3f4f6;
    font-family: Obviously,obviously-regular-fallback,sans-serif;
}
.tools h2{
    font-size: 40px;
    color: #f3f4f6;
    font-family: Obviously,obviously-regular-fallback,sans-serif;
}
.marq-b-twxt p{
    font-size: 18px;
    font-family: InterVariable,sans-serif;
    color: #f3f4f6;
}
.marq-img{
    margin: 40px 0;} 
.tools{
    margin-top: 51px;
    padding: 30px;
    background-color: #23262d;

}
.astro{
    background-color: #fff3;
    padding: 5px;
    border-radius: 5px;
}
.Sandbox{
    position: relative !important;
    padding: 24px !important;
    background-image: linear-gradient(83.21deg,#3245FF 0%,#B845ED 100%);
}
.sandbox-2{
    background-image: linear-gradient(247.23deg,#4AF2C8 0%,#2F4CB3 100%) !important;
}
.sandbox-2::before , .Sandbox::before , .sandbox-3::before , .Community::before{
    content: "";
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    position: absolute;
    background-image: url(https://astro.build/assets/noise.webp);
    opacity: .4;
}
.sandbox-3{
    background-image: linear-gradient(66.77deg,#D83333 0%,#F041FF 100%);
}
.Sandbox h5{
  font-size: 16px;
  color: #f1ecf3;
}
.Sandbox h2{
font-size: 40px;
color: #f1ecf3;
margin: 24px 0;
max-width: 250px;
}
.Sandbox p{
    font-family: InterVariable,sans-serif;
    font-size: 1rem;
    line-height: 1.5rem;
    color: #f1ecf3;
    font-weight: 300;
    max-width: 333px;

}
.Sandbox img{
    position: absolute;
    right: 20px;
    max-width: 132px;
}
.Sandbox-button:hover{
    background-color: rgba(255, 255, 255, 0.1);
}
.Sandbox-button{
    border-style: none;
    color: #B845ED;
    padding: 15px 50px;
    background-color: #ffffff;
    font-size: 18px;
    font-family: inherit;
    font-weight: 500;
    border-radius:100px ;
}
.Community p{
    font-size: 16px;
    font-family: "MD IO 0.5",md-io-fallback,monospace;
    font-weight: 300;
    color: #f2f6fa;
    /* margin: 24px 0; */
}
.community-join p{
    margin: 24px 0;
    font-family: InterVariable,sans-serif;
    color: #f2f6fa;
   font-size: 16px;
}
.community-join h2{
    font-family: Obviously,obviously-regular-fallback,sans-serif;
    font-size: 3.75rem;
    color: #f2f6fa;
    line-height: 66px;
}
.Community {
    background-image: linear-gradient(83.21deg,#3245FF 0%,#B845ED 100%);
    margin-top: 28px;
    position: relative;
    overflow: hidden;
}
.community-text{
    padding: 24px;
}
.community-join{
    padding: 0 32px 60px 32px;
}
img{
    width: 100%;
}
.community-img img {
    position: absolute;
    top: 10%;
    left: 22%;
}
